@import '../../index.scss';

.BGImg {
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  .BGImg-image {
    width: 100%;
    height: 100%;
    background-size: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 0;
    @include transition(opacity 0.2s);
  }
  &.BGImg-cover {
    .BGImg-image {
      background-size: cover !important;
    }
  }
  &.BGImg-loaded {
    .BGImg-image {
      opacity: 1;
    }
  }
}
